@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
/* Imports */

*,
* {
  margin: 0;
  padding: 0;
}
/* utilities */
.btn-black {
  background-color: black;
  border: black;
  border-radius: 10px;
  color: white;
  font-size: 1.5vw;
  text-transform: uppercase;
  padding: 0.5vw 1vw;
  transition: all 0.3s;
}
.btn-black:hover {
  background-color: rgb(32, 32, 32);
  border: rgb(32, 32, 32);
  transform: scale(1.1);
}
.btn-green {
  background-color: rgb(191, 247, 177);
  border: rgb(191, 247, 177);
  border-radius: 5px;
  color: white;
  font-size: 2vw;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin-left: auto;
  margin-bottom: 1vw;
  margin-right: auto;
  padding: 0.5vw 1vw 0vw;
  transition: all 0.3s;
  width: 70%;
}

.btn-white {
  background-color: rgb(230, 229, 229);
  border: white;
  border-radius: 5px;
  color: white;
  font-size: 2vw;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin-left: auto;
  margin-bottom: 1vw;
  margin-right: auto;
  padding: 0.5vw 1vw 0vw;
  transition: all 0.3s;
  width: 70%;
}
.btn-red {
  background-color: rgb(255, 221, 221);
  border: white;
  border-radius: 7px;
  color: white;
  font-size: 2vw;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin-left: auto;
  margin-bottom: 1vw;
  margin-right: auto;
  padding: 0.8vw 1vw 0vw;
  transition: all 0.3s;
  width: 70%;
}
a button:hover {
  transform: scale(1.1);
}
.text-center {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
/* General */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.body {
  background-color: rgb(238, 238, 238);
  background-size: cover;
}
/* Nav */
nav {
  background-color: black;
  color: white;
  display: flex;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  justify-content: space-evenly;
}
.nav-item {
  display: inline-block;
  font-size: 1.9vw;
  padding: 0.5vw 1vw;
}
.nav-title {
  font-size: 3vw;
  font-weight: bolder;
  font-family: "Kaushan Script";
  margin-left: 2vw;
  margin-right: 2vw;
}
/* Main */
.App {
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  color: black;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin-left: auto;
  margin-right: auto;
  padding: 2vw 2vw;
  width: 70%;
}
.container {
  margin-left: auto;
  margin-right: auto;
  width: 70%;
}
.formInputs {
  text-align: left;
}
.formInputs label {
  font-size: 1.8vw;
  font-weight: lighter;
}
.containerForm {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2vh;
  padding: 2vw 0vw;
  width: 85%;
}
.containerForm input {
  background-color: rgb(238, 238, 238);
  border: 2px solid rgb(238, 238, 238);
  border-radius: 5px;
  color: black;
  font-size: 1.5vw;
  margin-top: 1vw;
  padding: 0.5vw 1vw;
  width: 140%;
}
.containerForm input:focus {
  background-color: rgba(0, 0, 0, 0.034);
  outline: 0;
}

.container-button {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.containerForm button {
  padding: 0.7vw 2vw;
}
/* Header Band */
.container-header {
  width: 90%;
}
/* Band */
.container-band {
  background-image: url("https://images.pexels.com/photos/96380/pexels-photo-96380.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
  background-position: center;
  background-size: cover;
  border-radius: 10px;
  margin-bottom: 2vw;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}
.containerDataBand div {
  align-items: center;
  display: flex;
  justify-content: center;
}
.containerDataBand div span {
  font-weight: normal;
}
.background-black {
  align-items: center;
  background: rgba(0, 0, 0, 0.651);
  border-radius: 10px;
  color: white;
  width: 100%;
}
.containerDataBand {
  display: flex;
  justify-content: space-evenly;
  padding: 0.5vw 2vw 1vw 2vw;
}
.age-band,
.genre-band {
  text-align: left;
  font-weight: lighter;
}
.container-genre-band {
  display: flex;
  justify-content: center;
  align-items: center;
}
.title-band {
  font-size: 4.5vw;
  font-weight: bolder;
}
/* TOPS */
.contenedor-tops {
  display: grid;
  grid-template-columns: 50% 50%;
}
/* Songs */
.container-top-songs {
  text-align: left;
}
.container-top-songs h2 {
  margin-bottom: 1vw;
}
.song-name {
  font-size: 1.6vw;
  font-weight: normal;
}
.number-ranking {
  font-weight: bolder;
}
/* image */
.container-image {
  align-items: center;
  display: flex;
  justify-content: center;
}
.container-image img {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 12px 40px 0 rgba(0, 0, 0, 0.19);
  width: 60%;
}
/* Summary */
.container-paragraph-bio {
  margin-top: 4vw;
  margin-bottom: 2vw;
  text-align: left;
}
.container-paragraph-bio h1 {
  margin-top: 1vw;
}
.container-paragraph-bio p {
  margin-top: 1vw;
}
/* Albums */
.container-top-albums {
  display: grid;
  grid-template-columns: 50% 50%;
  margin-top: 2vw;
}
.container-top-albums hr {
  margin-bottom: 2vw;
}
.container-tops-albums {
  padding-top: 1vw;
}
.top-first {
  color: rgb(194, 165, 4);
  font-weight: bolder;
}
.top-second {
  color: rgb(151, 151, 151);
  font-weight: bold;
}
.top-third {
  color: chocolate;
  font-weight: bold;
}
/* Listen */
.container-listen-buttons {
  display: flex;
  justify-content: center;
  text-align: center;
}
.btn-search img {
  width: 90%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 800px) {
  h1 {
    font-size: 5vw;
  }
  h2 {
    font-size: 4vw;
  }
  h3 {
    font-size: 3vw;
  }
  h4 {
    font-size: 2vw;
  }
  h5 {
    font-size: 1vw;
  }

  /* General */
  .container {
    width: 95%;
  }
  .formInputs label {
    font-size: 5vw;
  }
  /* Nav */
  .nav-item {
    font-size: 5vw;
    padding: 2vw 2vw;
  }
  .nav-title {
    font-size: 7vw;
  }
  .App {
    width: 95%;
  }
  /* Form search */
  .containerForm input {
    font-size: 5vw;
    width: 100%;
  }
  .containerForm button {
    border-radius: 2px;
    font-size: 5vw;
    text-transform: capitalize;
    padding: 1vw 2vw;
  }
  /* Band */
  .container-band {
    border-radius: 3px;
    margin-top: 4vw;
  }
  .background-black {
    border-radius: 3px;
  }
  .container-header {
    width: 100%;
  }
  .containerDataBand {
    display: grid;
    grid-template-columns: 80% 20%;
    padding: 2vw 0.5vw 2vw 0vw;
  }
  .contenedor-tops {
    grid-template-columns: 70% 30%;
    margin-top: 6vw;
    margin-bottom: 6vw;
  }
  .title-band {
    font-size: 7vw;
  }
  /* Tops songs */
  .container-top-songs h2 {
    font-size: 5vw;
  }
  .container-top-songs h4 {
    font-size: 4vw;
  }
  .container-image img {
    width: 100%;
  }
  .container-image {
    display: block;
    text-align: right;
  }
  /* Summary */
  .container-paragraph-bio p {
    font-size: 4vw;
    overflow-x: hidden;
  }
  /* Listen and top albums */
  .container-top-albums {
    margin-top: 7vw;
    margin-bottom: 7vw;
  }
  .container-listen-buttons div a button {
    margin-bottom: 4vw;
  }
}

